<template>
  <div :class="classNames">
    <textarea
      v-bind="$attrs"
      :value="value"
      @input="changeHandler"
      @focus="focusHandler"
      ref="textarea"
      :style="{ height: height + 'px' }"
      :placeholder="placeholder"
    />
    <div class="Textarea__border" />
  </div>
</template>

<script>
import getClassName from '../../helpers/getClassName';
import classnames from '../../lib/classnames'
import requestAnimationFrame from '../../lib/requestAnimationFrame';

const baseClassName = getClassName('Textarea');

export default {
  data: () => ({
    height: 66,
  }),
  computed: {
    classNames () {
      return classnames(baseClassName)
    },
  },
  props: {
    value: {
      type: String,
    },
    // defaultValue: {
    //   type: String,
    //   default: '',
    // },
    grow: {
      type: Boolean,
      default: true,
    },
    // onChange: {
    //   type: Function,
    // },
    onResize: {
      type: Function,
      default: () => {}
    },
    placeholder: {
      type: String,
    },
  },
  mounted () {
    if (this.grow) {
      this.resize();
    }
  },
  methods: {
    changeHandler () {
      this.$emit('input', this.$refs.textarea.value)
      if (this.grow) {
        this.resize();
      }
    },
    focusHandler (e) {
      this.$emit('focus', e)
    },
    resize () {
      const el = this.$refs.textarea;

      if (el) {
        const { offsetHeight, scrollHeight } = el;
        const style = window.getComputedStyle(el);
        const paddingTop = parseInt(style.paddingTop);
        const paddingBottom = parseInt(style.paddingBottom);

        let diff = paddingTop + paddingBottom;

        if (scrollHeight + diff <= offsetHeight) {
          diff = 0;
        }

        if (el.value) {
          this.height = scrollHeight - diff;
        }

        // this.setState({ height: 0 }, () => {
          this.height = el.scrollHeight - diff;

          // this.setState({ height });

          this.onResize(el);
        // });
      }
    },
  }
}
</script>

<style>
@import '../../styles/common.css';
@import './Textarea.css';
</style>